<!--Auther liuhui
 Time 20200730  
 Dec 开始检查弹出框 -->
<template>
  <div class="check_dialog">
    <el-dialog
      :modal="false"
      :visible="dialogVisible"
      :modal-append-to-body="false"
      :append-to-body="true"
      :show-close="false"
      :close-on-click-modal="false"
      top="27vh"
      @close="onClose()"
    >
      <div slot="title" class="title_class">
        <span>
          <img src="/images/config_dlg_title.png" alt />
        </span>
        <span>涉密文件检查配置</span>
      </div>
      <div class="content_class">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="检查类型" name="first">
            <div class="check_type">
              <span>检查范围</span>
            </div>
            <div class="time_class common">
              <el-checkbox v-model="timeChecked" @change="unlimitedTimeChange">不限时间</el-checkbox>
              <el-radio v-model="radio" label="1" :disabled="timeDiable">创建时间</el-radio>
              <el-radio v-model="radio" label="2 " :disabled="timeDiable">修改时间</el-radio>
              <div style="padding:10px 0 0">
                <span style="padding: 0 13px 0 18px;">开始时间</span>
                <el-date-picker
                  v-model="startTime"
                  type="datetime"
                  placeholder="选择日期时间"
                  size="mini"
                  :disabled="timeDiable"
                ></el-date-picker>
                <span style="padding:0 18px 0 18px ">结束时间</span>
                <el-date-picker
                  v-model="endTime"
                  type="datetime"
                  placeholder="选择日期时间"
                  size="mini"
                  :disabled="timeDiable"
                ></el-date-picker>
              </div>
            </div>
            <div class="computer_class common">
              <el-checkbox v-model="computerChecked" @change="myComputerChange">我的电脑</el-checkbox>
              <el-input
                v-model="computerIpt"
                placeholder="C:D::E:F"
                size="mini"
                style="width:300px;margin:0 8px 0 0"
                :disabled="compDisable"
              ></el-input>
              <el-button size="mini" :disabled="compDisable" @click="addUrl">添加</el-button>
              <el-button size="mini" :disabled="compDisable" @click="glanceOver">浏览</el-button>
              <!-- <input type="file" name="file" value /> -->
              <el-button size="mini" :disabled="compDisable" @click="clearUrl">清空</el-button>

              <el-table
                :data="tableData"
                stripe
                style="width: 100%;height: 97px;overflow: auto;margin-top:10px"
                size="mini"
                border
                :disabled="compDisable"
                align="center"
              >
                <el-table-column label="序号" width="50" type="index"></el-table-column>
                <el-table-column prop="path" label="检查路径"></el-table-column>
              </el-table>
            </div>
            <div class="nourl_class common">
              <el-checkbox v-model="urlChecked" @change="noContainsUrl">不包含以下路径</el-checkbox>
              <el-input
                v-model="urlIpt"
                size="mini"
                style="width:300px;margin:0 8px"
                :disabled="urlDisable"
              ></el-input>
              <el-button size="mini" :disabled="urlDisable" @click="addNotUrl">添加</el-button>
              <el-button size="mini" :disabled="urlDisable" @click="clearNotUrl">清空</el-button>
            </div>
            <div class="common">
              <el-radio v-model="radioHit" label="1" @change="hitDisable=true">命中即返回</el-radio>
              <el-radio v-model="radioHit" label="2" @change="hitDisable=true">查找所有</el-radio>
              <el-radio v-model="radioHit" label="3" @change="hitDisable=false">检查前</el-radio>
              <el-input v-model="chooseIpt" size="mini" style="width:100px" :disabled="hitDisable"></el-input>
              <span style="padding-left:5px">字符</span>
            </div>
            <div class="common">
              <el-checkbox v-model="findChecked">查找文件名称</el-checkbox>
              <el-checkbox v-model="findChecked">查找文件内容</el-checkbox>
              <el-checkbox>空闲时检查</el-checkbox>摘要长度:(范围:200~2000)
              <el-input v-model="abstractIpt" size="mini" style="width:60px" placeholder="400"></el-input>
            </div>
          </el-tab-pane>
          <el-tab-pane label="文件类型" name="second">
            <div class="check_type">
              <span>常规文件</span>
            </div>
            <div class="common">
              <div>
                <el-checkbox style="height:26px;margin-left:110px">在选中类型中检查是否加密</el-checkbox>
              </div>
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate"
                    @change="officeCheckAllChange"
                    v-model="officeCheckAll"
                  >微软Office文档</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkOffices"
                    @change="handleCheckedOfficesChange"
                    class="office_cla"
                  >
                    <el-checkbox v-for="item in officesData" :key="item.name" :label="item">
                      {{item.name}}
                      <span>({{item.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate1"
                    @change="wpsCheckAllChange"
                    v-model="wpsCheckAll"
                    style="padding-right:4px"
                  >金山WPS文档</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkWpss"
                    @change="handleCheckedWpssChange"
                    class="office_cla"
                  >
                    <el-checkbox v-for="item in wpssData" :key="item.name" :label="item">
                      {{item.name}}
                      <span>({{item.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate2"
                    @change="otherCheckAllChange"
                    v-model="otherCheckAll"
                    style="padding-right:7px"
                  >其他格式文档</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkothers"
                    @change="handleCheckedOthersChange"
                    class="office_cla"
                  >
                    <el-checkbox v-for="item in othersData" :key="item.name" :label="item">
                      {{item.name}}
                      <span>({{item.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate3"
                    @change="compressCheckAllChange"
                    v-model="compressCheckAll"
                    style="padding-right:31px"
                  >压缩文档</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkCompress"
                    @change="handleCheckedCompressChange"
                    class="office_cla"
                  >
                    <el-checkbox v-for="item in compressData" :key="item.name" :label="item">
                      {{item.name}}
                      <span>({{item.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
              <div>
                <el-checkbox @change="tuozhan">扩展文件类型</el-checkbox>
                <el-input
                  v-model="computerIpt"
                  placeholder="请输入后缀名,多个后缀名之间以空格隔开"
                  size="mini"
                  style="width:300px;margin:0 8px"
                  :disabled="tuozhanDisable"
                ></el-input>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="关键词配置" name="third" style="padding:5px 15px">
            <div v-if="show">
              <div class="common" style="display: inline-block;">
                <el-checkbox v-model="findChecked" @change="change">高级配置</el-checkbox>
              </div>
              <div style="display: inline-block;float:right;padding:5px 20px 0 0 ">
                <el-button size="mini" class="btn" @click="addkeys">添 加</el-button>
              </div>
              <div class="flex_class common">
                <div>
                  包含以下全部关键词
                  <el-input v-model="allKeys" size="mini" style="width:300px;margin:0 8px"></el-input>
                </div>
                <el-button size="mini" class="btn" @click="showDialog = true">载入关键词库</el-button>
              </div>
              <div class="flex_class common">
                <div>
                  包含以下任意关键词
                  <el-input v-model="wantonlyKeys" size="mini" style="width:300px;margin:0 8px"></el-input>
                </div>
                <el-button size="mini" class="btn" @click="showDialog = true">载入关键词库</el-button>
              </div>
              <div class="flex_class common">
                <div>
                  不包含以下关键词
                  <el-input
                    v-model="notContainKeys"
                    size="mini"
                    style="width:300px;margin: 0px 8px 0 21px"
                  ></el-input>
                </div>
                <el-button size="mini" class="btn" @click="showDialog = true">载入关键词库</el-button>
              </div>
              <el-table
                :data="keysData"
                stripe
                style="width: 100%;height: 180px;overflow: auto;margin-top:8px"
                size="mini"
                border
                :disabled="compDisable"
              >
                <el-table-column
                  prop="keyCompose"
                  label="关键词组合('或'关系显示为'|','与'关系显示为'&','非'关系显示为'!')"
                  header-align="center"
                ></el-table-column>
              </el-table>
            </div>
            <div v-if="!show">
              <div class="common" style="display: inline-block;">
                <el-checkbox v-model="findChecked" @change="change">高级配置</el-checkbox>
              </div>
              <el-button size="mini" class="btn_key" @click="showDialog = true">载入关键词库</el-button>
              <span style="margin-left:30px">或'关系用空格,显示为'|'。</span>

              <div class="flex_class common">
                <div>
                  自定义关键词
                  <el-input v-model="myselfKeys" size="mini" style="width:430px;margin:0 8px"></el-input>
                </div>
                <el-button size="mini" class="btn_add" @click="addMyselfkeys">添 加</el-button>
              </div>
              <el-table
                :data="myselfData"
                stripe
                style="width: 100%;height: 180px;overflow: auto;margin-top:8px"
                size="mini"
                border
                :disabled="compDisable"
              >
                <el-table-column prop="trade" label="行业"></el-table-column>
                <el-table-column prop="category" label="分类"></el-table-column>
                <el-table-column prop="keyWords" label="关键词组"></el-table-column>
              </el-table>
            </div>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div slot="footer" class="footer_class">
        <el-button size="mini" @click="onClose()">开始检查</el-button>
        <el-button size="mini" @click="onClose()">使用默认设置</el-button>
        <el-button size="mini" @click="onClose()">取 消</el-button>
      </div>
    </el-dialog>
    <tip :tipVisible.sync="errorFlag" :msg="msg"></tip>
    <keys-dialog :dialogVisible.sync="showDialog"></keys-dialog>
  </div>
</template>

<script>
import keysDialog from "./keysDialog";
import tip from "./tip";

export default {
  props: ["dialogVisible"],
  components: { keysDialog, tip },
  data() {
    return {
      activeName: "third",
      timeChecked: true, //不限时间选框
      computerChecked: true, //我的电脑选框
      radio: "2",
      timeDiable: true, //不限时间输入框
      startTime: "", //开始时间
      endTime: "", //结束时间
      computerIpt: "", //我的电脑输入框
      compDisable: true, //我的电脑输入框
      urlDisable: true, //不包含路径
      urlChecked: false, //不包含路径选框
      urlIpt: "", //不包含路径输入框
      chooseIpt: "", //字符输入框
      findChecked: true,
      abstractIpt: "", //摘要输入框
      allKeys: "", //包含以下全部关键词
      wantonlyKeys: "", //包含以下任意关键词
      notContainKeys: "", //不包含以下关键词
      tableData: [],
      keysData: [],
      showDialog: false,
      radioHit: "1",
      hitDisable: true,
      tuozhanDisable: true, //拓展文件类型
      weiruanCheck: false,
      weiruanCheckAll: false,
      errorFlag: false,
      msg: "",
      show: true,
      myselfKeys: "", //自定义关键词
      myselfData: [], //自定义关键词数据
      // office
      officesData: [
        {
          name: "微软文字文档",
          type: "doc;docx"
        },
        {
          name: "微软图标文档",
          type: "xls;xlsx"
        },
        {
          name: "微软演示文档",
          type: "ppt;pptx;pps"
        },

        {
          name: "rtf文档",
          type: "rtf"
        },
        {
          name: "文本文档",
          type: "txt"
        },
        {
          name: "微软模拟文档",
          type: "dot;dotx"
        }
      ],
      officeCheckAll: false,
      isIndeterminate: true,
      checkOffices: [],
      // 金山wps文档
      wpssData: [
        {
          name: "金山文字文档",
          type: "wps"
        },
        {
          name: "金山图表文档",
          type: "et"
        },
        {
          name: "金山演示文档",
          type: "dps"
        }
      ],
      wpsCheckAll: false,
      isIndeterminate1: true,
      checkWpss: [],
      // 其他格式文档
      othersData: [
        {
          name: "iso文档",
          type: "iso"
        },
        {
          name: "pdf文档",
          type: "pdf"
        },
        {
          name: "网页文档",
          type: "htm;html;mht;mhtml;xml"
        },
        {
          name: "方正书版文档",
          type: "fbd"
        },
        {
          name: "dwg文档",
          type: "dwg"
        }
      ],
      otherCheckAll: false,
      isIndeterminate2: true,
      checkothers: [],
      // 压缩文档
      compressData: [
        {
          name: "压缩文档",
          type: "在压缩文档(在压缩文档(zip;7z;rar;tar)中查找以上格式"
        }
      ],
      compressCheckAll: false,
      isIndeterminate3: true,
      checkCompress: []
    };
  },
  methods: {
    // 取消添加弹框
    onClose() {
      if (this.dialogVisible) {
        this.$emit("update:dialogVisible", false);
      }
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 不限时间选框按钮
    unlimitedTimeChange(val) {
      console.log(val);
      this.timeDiable = !this.timeDiable;
    },
    // 我的电脑选框按钮
    myComputerChange() {
      this.compDisable = !this.compDisable;
    },
    // 我的电脑添加按钮
    addUrl() {
      if (this.computerIpt != "") {
        let flag = true;
        this.tableData.map(item => {
          if (this.computerIpt == item.path) {
            flag = false;
          }
        });
        if (flag) {
          this.tableData.push({ path: this.computerIpt });
        } else {
          this.msg = "所选路径已存在,不能重复添加!";

          this.errorFlag = true;
        }
      }
    },
    // 清空路径
    clearUrl() {
      this.tableData = [];
    },
    addNotUrl() {},
    clearNotUrl() {},
    // 不包含以下路径
    noContainsUrl() {
      this.urlDisable = !this.urlDisable;
    },
    //添加关键词
    addkeys() {
      let keyword = "";
      if (this.allKeys) keyword += this.allKeys + "|";

      if (this.wantonlyKeys) keyword += this.wantonlyKeys + "|";

      if (this.notContainKeys) keyword += this.notContainKeys + "|";
      if (!keyword) {
        // this.$message.error("请输入内容");
        // this.$message({
        //   message: "请输入内容",
        //   type: "error",
        //   offset: "280"
        // });
      } else {
        let flag = true;
        keyword = keyword.substr(0, keyword.length - 1);
        this.keysData.map(item => {
          if (keyword == item.keyCompose) {
            flag = false;
          }
        });
        if (flag) {
          this.keysData.push({ keyCompose: keyword });
        } else {
          this.msg = "不能重复添加!";
          this.errorFlag = true;
        }
      }
    },
    //添加自定义关键词
    addMyselfkeys() {
      let flag = true;
      if (this.myselfKeys) {
        this.myselfData.map(item => {
          if (this.myselfKeys == item.keyWords) {
            flag = false;
          }
        });
      } else {
        this.msg = "关键词不能为空!";
        this.errorFlag = true;
      }
      if (flag) {
        this.myselfData.push({
          trade: "自定义关键词组",
          keyWords: this.myselfKeys
        });
      } else {
        this.msg = "不能重复添加!";
        this.errorFlag = true;
      }
    },
    // 拓展选框
    tuozhan(val) {
      this.tuozhanDisable = !val;
    },
    weiruanChange() {
      // console.log(val, "111");
      // this.weiruanCheck = val;
      // this.$nextTick(() => {
      //   this.aa = !this.aa;
      // });
    },
    handleClose() {},
    // 浏览
    glanceOver() {},
    change(val) {
      console.log(val);
      this.show = !this.show;
    },
    // office格式全部选择
    officeCheckAllChange(val) {
      this.checkOffices = val ? this.officesData : [];
      this.isIndeterminate = false;
    },
    //单个选择
    handleCheckedOfficesChange(value) {
      let checkedCount = value.length;
      this.officeCheckAll = checkedCount === this.officesData.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.officesData.length;
    },
    //金山WPS文档全部选择
    wpsCheckAllChange(val) {
      this.checkWpss = val ? this.wpssData : [];
      this.isIndeterminate1 = false;
    },
    //单个选择
    handleCheckedWpssChange(value) {
      let checkedCount = value.length;
      this.wpsCheckAll = checkedCount === this.wpssData.length;
      this.isIndeterminate1 =
        checkedCount > 0 && checkedCount < this.wpssData.length;
    },
    //其他格式文档全部选择
    otherCheckAllChange(val) {
      this.checkothers = val ? this.othersData : [];
      this.isIndeterminate2 = false;
    },
    //单个选择
    handleCheckedOthersChange(value) {
      let checkedCount = value.length;
      this.otherCheckAll = checkedCount === this.othersData.length;
      this.isIndeterminate2 =
        checkedCount > 0 && checkedCount < this.othersData.length;
    },
    //压缩文档全部选择
    compressCheckAllChange(val) {
      this.checkCompress = val ? this.compressData : [];
      this.isIndeterminate3 = false;
    },
    //单个选择
    handleCheckedCompressChange(value) {
      let checkedCount = value.length;
      this.compressCheckAll = checkedCount === this.compressData.length;
      this.isIndeterminate3 =
        checkedCount > 0 && checkedCount < this.compressData.length;
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
/deep/.el-dialog {
  width: 740px;
  height: 545px;
}
/deep/.el-table--mini td,
/deep/.el-table--mini th {
  padding: 0;
}
/deep/.el-table td,
/deep/.el-table th {
  text-align: center;
  padding: 0;
  height: 26px;
  line-height: 26px;
  color: #606266;
}
/deep/.el-dialog__header {
  height: 70px;
  padding: 0;
  background: url(/images/dialog_bg1.png) repeat;
  border-bottom: 7px solid #8e0203;
}
/deep/.el-dialog__body {
  background: #e4eef8;
  padding: 17px 28px 6px 28px;
  overflow: auto;
}

/deep/.el-dialog__footer {
  background: #e4eef8;

  padding: 0 28px 6px;
}
/deep/.el-tabs__header {
  margin: 0;
}
/deep/.el-radio__label,
/deep/.el-checkbox__label {
  font-size: 12px;
}
/deep/.el-button--mini {
  padding: 5px 13px;
}
.title_class {
  line-height: 70px;

  margin: auto;
  > span:nth-child(2) {
    line-height: 40px;
    font-size: 22px;
    font-weight: 800;
    line-height: 70px;
  }
  display: flex;
  justify-content: center;
}
.el-icon-picture-outline-round {
  padding-right: 3px;
  border-right: 1px solid;
  font-size: 40px;
}
.footer_class {
  display: flex;
  justify-content: space-around;
}
.content_class {
  margin: auto;
  background: #fff;
  padding: 5px;
  font-size: 12px;
}
/deep/.el-tabs__content {
  height: 364px;
  overflow: auto;
}
/deep/.el-checkbox__label {
  padding-left: 4px;
  padding-right: 4px;
}
/deep/.el-message {
  top: 280px !important;
  z-index: 2005;
}
/deep/.check_dispose {
  top: 4px;
}
/deep/.el-input__inner {
  height: 24px;
  line-height: 24px;
}
/deep/.el-checkbox {
  margin-right: 10px;
}
.el-tab-pane {
  margin-top: 5px;
}
// 内容样式
.content_class {
  .check_type {
    margin: 10px 0;
    background: url(/images/config_item_bk.png) no-repeat;
    position: relative;
    height: 37px;
    span {
      position: absolute;
      left: 32px;
      top: 13px;
      color: #fff;
    }
  }
  .common {
    padding: 10px 20px 0;
  }
  .tab_class {
    height: 273px;
  }
  .row {
    vertical-align: top;
    padding-top: 8px;
  }
  .flex_class {
    display: flex;
    justify-content: space-between;
  }
  .sp_cla {
    color: #757575;
  }
  .btn {
    width: 150px;
  }
  .btn_key {
    width: 120px;
  }
  .btn_add {
    width: 80px;
  }
}
.layout_class {
  display: flex;
  .office_cla {
    display: flex;
    flex-wrap: wrap;
    label {
      width: 150px;
      height: 28px;
    }
  }
}
</style> 
