 Time 20200730  
 Dec 开始检查弹出框 -->
<template>
  <div class="check_dialog">
    <el-dialog
      :modal="false"
      :visible="dialogVisible"
      :modal-append-to-body="false"
      :append-to-body="true"
      :show-close="false"
      :close-on-click-modal="false"
      top="27vh"
      @close="onClose()"
    >
      <div slot="title" class="title_class">
        <span>
          <img src="/images/config_dlg_title.png" alt />
        </span>
        <span>涉密文件检查配置</span>
      </div>
      <div class="content_class">
        <el-tabs v-model="activeName" type="card" @tab-click="handleClick">
          <el-tab-pane label="检查类型" name="first">
            <div class="check_type">
              <span>检查范围</span>
            </div>
            <div class="time_class common">
              <el-checkbox v-model="timeChecked" @change="unlimitedTimeChange">不限时间</el-checkbox>
              <el-radio v-model="radio" label="1" :disabled="timeDiable">创建时间</el-radio>
              <el-radio v-model="radio" label="2 " :disabled="timeDiable">修改时间</el-radio>
              <div style="padding:10px 0 0">
                <span style="padding: 0 13px 0 18px;">开始时间</span>
                <el-date-picker
                  v-model="startTime"
                  type="datetime"
                  placeholder="选择日期时间"
                  size="mini"
                  :disabled="timeDiable"
                ></el-date-picker>
                <span style="padding:0 18px 0 18px ">结束时间</span>
                <el-date-picker
                  v-model="endTime"
                  type="datetime"
                  placeholder="选择日期时间"
                  size="mini"
                  :disabled="timeDiable"
                ></el-date-picker>
              </div>
            </div>
            <div class="computer_class common">
              <el-checkbox v-model="computerChecked" @change="myComputerChange">我的电脑</el-checkbox>
              <el-input
                v-model="computerIpt"
                placeholder="C:D::E:F"
                size="mini"
                style="width:300px;margin:0 8px 0 0"
                :disabled="compDisable"
              ></el-input>
              <el-button size="mini" :disabled="compDisable" @click="addUrl">添加</el-button>
              <el-button size="mini" :disabled="compDisable" @click="glanceOver">浏览</el-button>
              <!-- <input type="file" name="file" value /> -->
              <el-button size="mini" :disabled="compDisable" @click="clearUrl">清空</el-button>

              <el-table
                :data="tableData"
                stripe
                style="width: 100%;height: 97px;overflow: auto;margin-top:10px"
                size="mini"
                border
                :disabled="compDisable"
              >
                <el-table-column label="序号" width="50" type="index"></el-table-column>
                <el-table-column prop="path" label="检查路径"></el-table-column>
              </el-table>
            </div>
            <div class="nourl_class common">
              <el-checkbox v-model="urlChecked" @change="noContainsUrl">不包含以下路径</el-checkbox>
              <el-input
                v-model="urlIpt"
                size="mini"
                style="width:300px;margin:0 8px"
                :disabled="urlDisable"
              ></el-input>
              <el-button size="mini" :disabled="urlDisable" @click="addNotUrl">添加</el-button>
              <el-button size="mini" :disabled="urlDisable" @click="clearNotUrl">清空</el-button>
            </div>
            <div class="common">
              <el-radio v-model="radioHit" label="1" @change="hitDisable=true">命中即返回</el-radio>
              <el-radio v-model="radioHit" label="2" @change="hitDisable=true">查找所有</el-radio>
              <el-radio v-model="radioHit" label="3" @change="hitDisable=false">检查前</el-radio>
              <el-input v-model="chooseIpt" size="mini" style="width:100px" :disabled="hitDisable"></el-input>
              <span style="padding-left:5px">字符</span>
            </div>
            <div class="common">
              <!-- <el-checkbox v-model="findChecked">查找文件名称</el-checkbox>
              <el-checkbox v-model="findChecked">查找文件内容</el-checkbox>-->
              <el-checkbox>空闲时检查</el-checkbox>摘要长度:(范围:200~2000)
              <el-input
                v-model="abstractIpt"
                size="mini"
                style="width:60px;margin-left:10px"
                placeholder="400"
              ></el-input>
              <span style="margin-left:20px">最小图片</span>
              <el-input size="mini" style="width:50px;margin:0 5px" placeholder="0"></el-input>k
              <span style="margin-left:20px">最大图片</span>
              <el-input size="mini" style="width:50px;margin:0 5px" placeholder="0"></el-input>k
            </div>
          </el-tab-pane>
          <el-tab-pane label="文件类型" name="second">
            <div class="check_type">
              <span>常规文件</span>
            </div>
            <div class="common">
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate"
                    @change="layoutCheckAllChange"
                    v-model="layoutCheckAll"
                  >所有图片格式</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkLayouts"
                    @change="handleCheckedLayoutsChange"
                    class="lay_cla"
                  >
                    <el-checkbox v-for="layout in layoutPicData" :key="layout.name" :label="layout">
                      {{layout.name}}
                      <span>({{layout.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
              <div class="layout_class">
                <div>
                  <el-checkbox
                    :indeterminate="isIndeterminate1"
                    @change="wordCheckAllChange"
                    v-model="wordCheckAll"
                  >文档中的图片</el-checkbox>
                </div>
                <div>
                  <el-checkbox-group
                    v-model="checkWords"
                    @change="handleCheckedWordsChange"
                    class="word_cla"
                  >
                    <el-checkbox v-for="word in wordData" :key="word.name" :label="word">
                      {{word.name}}
                      <span>({{word.type}})</span>
                    </el-checkbox>
                  </el-checkbox-group>
                </div>
              </div>
            </div>
          </el-tab-pane>
          <el-tab-pane label="关键词配置" name="third" style="padding:5px 15px">
            <div class="flex_class common">
              <el-checkbox v-model="findChecked">高级配置</el-checkbox>
              <el-button size="mini" class="btn" @click="addkeys">添 加</el-button>
            </div>
            <div class="flex_class common">
              <div>
                包含以下全部关键词
                <el-input v-model="allKeys" size="mini" style="width:300px;margin:0 8px"></el-input>
              </div>
              <el-button size="mini" class="btn" @click="showDialog = true">载入关键词库</el-button>
            </div>
            <div class="flex_class common">
              <div>
                包含以下任意关键词
                <el-input v-model="wantonlyKeys" size="mini" style="width:300px;margin:0 8px"></el-input>
              </div>
              <el-button size="mini" class="btn">载入关键词库</el-button>
            </div>
            <div class="flex_class common">
              <div>
                不包含以下关键词
                <el-input
                  v-model="notContainKeys"
                  size="mini"
                  style="width:300px;margin: 0px 8px 0 21px"
                ></el-input>
              </div>
              <el-button size="mini" class="btn">载入关键词库</el-button>
            </div>
            <el-table
              :data="keysData"
              stripe
              style="width: 100%;height: 180px;overflow: auto;margin-top:8px"
              size="mini"
              border
              :disabled="compDisable"
            >
              <el-table-column
                prop="keyCompose"
                label="关键词组合('或'关系显示为'|','与'关系显示为'&','非'关系显示为'!')"
                header-align="center"
              ></el-table-column>
            </el-table>
          </el-tab-pane>
        </el-tabs>
      </div>
      <div slot="footer" class="footer_class">
        <el-button size="mini" @click="onClose()">开始检查</el-button>
        <el-button size="mini" @click="onClose()">使用默认设置</el-button>
        <el-button size="mini" @click="onClose()">取 消</el-button>
      </div>
    </el-dialog>
    <tip :tipVisible.sync="errorFlag" :msg="msg"></tip>
    <keys-dialog :dialogVisible.sync="showDialog"></keys-dialog>
  </div>
</template>

<script>
import keysDialog from "./keysDialog";
import tip from "./tip";

export default {
  props: ["dialogVisible"],
  components: { keysDialog, tip },
  data() {
    return {
      activeName: "third",
      timeChecked: true, //不限时间选框
      computerChecked: true, //我的电脑选框
      radio: "2",
      timeDiable: true, //不限时间输入框
      startTime: "", //开始时间
      endTime: "", //结束时间
      computerIpt: "", //我的电脑输入框
      compDisable: true, //我的电脑输入框
      urlDisable: true, //不包含路径
      urlChecked: false, //不包含路径选框
      urlIpt: "", //不包含路径输入框
      chooseIpt: "", //字符输入框
      findChecked: true,
      abstractIpt: "", //摘要输入框
      allKeys: "", //包含以下全部关键词
      wantonlyKeys: "", //包含以下任意关键词
      notContainKeys: "", //不包含以下关键词
      tableData: [],
      keysData: [],
      showDialog: false,
      radioHit: "1",
      hitDisable: true,
      tuozhanDisable: true, //拓展文件类型
      weiruanCheck: false,
      weiruanCheckAll: false,
      errorFlag: false,
      msg: "",
      // 图片文件---所有图片格式数据
      layoutPicData: [
        {
          name: "jpg文件",
          type: "jpg;jpeg"
        },
        {
          name: "bmp文件",
          type: "bmp"
        },
        {
          name: "pig文件",
          type: "pig"
        },

        {
          name: "dib文件",
          type: "dib"
        },
        {
          name: "gif文件",
          type: "gif"
        },
        {
          name: "tif文件",
          type: "tif;tiff"
        },
        {
          name: "png文件",
          type: "png"
        },
        {
          name: "psd文件",
          type: "psd"
        },
        {
          name: "raw文件",
          type: "raw"
        }
      ],
      checkLayouts: [],
      layoutCheckAll: false,
      isIndeterminate: true,
      // 文档图片
      wordData: [
        {
          name: "文字文档中图片",
          type: "doc;docx;wps"
        },
        {
          name: "图表文档中图片",
          type: "xls;xlsxl;et"
        },
        {
          name: "演示文档中图片",
          type: "ppt;pptx;pps;dps"
        },
        {
          name: " pdf文档中图片",
          type: "pdf"
        },
        {
          name: "网页文档中图片",
          type: "mht;mhtml"
        }
      ],
      checkWords: [],
      wordCheckAll: false,
      isIndeterminate1: true
    };
  },
  methods: {
    // 取消添加弹框
    onClose() {
      if (this.dialogVisible) {
        this.$emit("update:dialogVisible", false);
      }
    },
    handleClick(tab, event) {
      console.log(tab, event);
    },
    // 不限时间选框按钮
    unlimitedTimeChange(val) {
      console.log(val);
      this.timeDiable = !this.timeDiable;
    },
    // 我的电脑选框按钮
    myComputerChange() {
      this.compDisable = !this.compDisable;
    },
    // 我的电脑添加按钮
    addUrl() {
      let flag = true;
      this.tableData.map(item => {
        if (this.computerIpt == item.path) {
          flag = false;
        }
      });
      if (flag) {
        this.tableData.push({ path: this.computerIpt });
      } else {
        this.msg = "所选路径已存在,不能重复添加!";

        this.errorFlag = true;
      }
    },
    // 清空路径
    clearUrl() {
      this.tableData = [];
    },
    addNotUrl() {},
    clearNotUrl() {},
    // 不包含以下路径
    noContainsUrl() {
      this.urlDisable = !this.urlDisable;
    },
    //添加关键词
    addkeys() {
      let keyword = "";
      if (this.allKeys) keyword += this.allKeys + "|";

      if (this.wantonlyKeys) keyword += this.wantonlyKeys + "|";

      if (this.notContainKeys) keyword += this.notContainKeys + "|";
      if (!keyword) {
        // this.$message.error("请输入内容");
        // this.$message({
        //   message: "请输入内容",
        //   type: "error",
        //   offset: "280"
        // });
      } else {
        let flag = true;
        keyword = keyword.substr(0, keyword.length - 1);
        this.keysData.map(item => {
          if (keyword == item.keyCompose) {
            flag = false;
          }
        });
        if (flag) {
          this.keysData.push({ keyCompose: keyword });
        } else {
          this.msg = "不能重复添加!";
          this.errorFlag = true;
        }
      }
    },
    // 拓展选框
    tuozhan(val) {
      this.tuozhanDisable = !val;
    },
    weiruanChange() {
      // console.log(val, "111");
      // this.weiruanCheck = val;
      // this.$nextTick(() => {
      //   this.aa = !this.aa;
      // });
    },
    handleClose() {},
    //浏览
    glanceOver() {},
    // 图片格式全部选择
    layoutCheckAllChange(val) {
      this.checkLayouts = val ? this.layoutPicData : [];
      this.isIndeterminate = false;
    },
    //单个选择
    handleCheckedLayoutsChange(value) {
      let checkedCount = value.length;
      this.layoutCheckAll = checkedCount === this.layoutPicData.length;
      this.isIndeterminate =
        checkedCount > 0 && checkedCount < this.layoutPicData.length;
    },
    //文档中图片全部选择
    wordCheckAllChange(val) {
      this.checkWords = val ? this.wordData : [];
      this.isIndeterminate1 = false;
    },
    //单个选择
    handleCheckedWordsChange(value) {
      let checkedCount = value.length;
      this.wordCheckAll = checkedCount === this.wordData.length;
      this.isIndeterminate1 =
        checkedCount > 0 && checkedCount < this.wordData.length;
    }
  },
  computed: {}
};
</script>

<style lang="less" scoped>
/deep/.el-dialog {
  width: 740px;
  height: 545px;
}
/deep/.el-table--mini td,
/deep/.el-table--mini th {
  padding: 0;
}
/deep/.el-dialog__header {
  height: 70px;
  padding: 0;
  background: url(/images/dialog_bg1.png) repeat;
  border-bottom: 7px solid #8e0203;
}
/deep/.el-dialog__body {
  background: #e4eef8;
  padding: 17px 28px 6px 28px;
  overflow: auto;
}

/deep/.el-dialog__footer {
  background: #e4eef8;

  padding: 0 28px 6px;
}
/deep/.el-tabs__header {
  margin: 0;
}
/deep/.el-radio__label,
/deep/.el-checkbox__label {
  font-size: 12px;
}
/deep/.el-button--mini {
  padding: 5px 13px;
}

.title_class {
  line-height: 70px;

  margin: auto;
  > span:nth-child(2) {
    line-height: 40px;
    font-size: 22px;
    font-weight: 800;
    line-height: 70px;
  }
  display: flex;
  justify-content: center;
}
.el-icon-picture-outline-round {
  padding-right: 3px;
  border-right: 1px solid;
  font-size: 40px;
}
.footer_class {
  display: flex;
  justify-content: space-around;
}
.content_class {
  margin: auto;
  background: #fff;
  padding: 5px;
  font-size: 12px;
}
/deep/.el-tabs__content {
  height: 364px;
  overflow: auto;
}
/deep/.el-checkbox__label {
  padding-left: 4px;
  padding-right: 4px;
}
/deep/.el-message {
  top: 280px !important;
  z-index: 2005;
}
/deep/.check_dispose {
  top: 4px;
}
/deep/.el-input__inner {
  height: 24px;
  line-height: 24px;
}
/deep/.el-checkbox {
  margin-right: 10px;
}
.el-tab-pane {
  margin-top: 5px;
}

// 内容样式
.content_class {
  .check_type {
    margin: 10px 0;
    background: url(/images/config_item_bk.png) no-repeat;
    position: relative;
    height: 37px;
    span {
      position: absolute;
      left: 32px;
      top: 13px;
      color: #fff;
    }
  }
  .common {
    padding: 10px 20px 0;
  }
  .tab_class {
    height: 273px;
  }
  .row {
    vertical-align: top;
    padding-top: 8px;
  }
  .flex_class {
    display: flex;
    justify-content: space-between;
  }

  .btn {
    width: 150px;
  }
}
.layout_class {
  display: flex;
  .lay_cla {
    display: flex;
    flex-wrap: wrap;
    label {
      width: 120px;
      height: 28px;
    }
  }
  .word_cla {
    display: flex;
    flex-wrap: wrap;
    label {
      width: 250px;
      height: 28px;
    }
  }
}
</style> 
